.header {
    margin: 45px;
    display: flex;
    justify-content: space-between;
}
.header-text {
    color: antiquewhite;
    font-size: 70px;
    margin-right: 50px;
}
.header-text h1 {
    color: #6fd7f0;
    margin-left: 50px;
}
.header-text h3 {
    font-size: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 55px;
}
.header-text p {
    font-size: 20px;
}
.header-imgs {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 0px;
    padding: 0px;
    margin-right: 0px;
}
.header-imgs-rows {
    display: flex;
    padding: 0px;
    width: fit-content;
    height: fit-content;
}
.header-img {
    margin: 0px;
    width: 0px;
    height: 0px;
}

.infoPanel {
    padding: 0px;
    padding-top: 0px;
    padding-bottom: 0px;
    background-color: #171E2E;
    margin-left: 0px;
    margin-right: 0px;
    display: flex;
    color: #c05537;
}
.infoPanel-text h1 {
    font-size: 0px;
    color: saddlebrown;
    margin-bottom: 30px;
    margin-left: 250px;
    text-shadow: 1px 2px 1px black;
    text-align: center;
}
.infoPanel-text h3 {
    font-size: 0px;
    margin-left: 280px;
    text-shadow: 1px 2px 1px black;
}
.infoPanel-img {
    height: 0px;
    margin: 0px;
    padding: 0px;
}

a {
    text-decoration: none;
    color: rgb(111, 111, 231);
}
a:visited {
    color: rgb(111, 111, 231);
}

.mintButton {
        flex-direction: "column";
        background-color: "#ff8932";
        padding: 24;
        padding-right: 10;
        padding-top: 0;
        border-style: "solid";
        border-color: "black";
        border-width: 10;
        border-radius: 10;
        font-size: 40;
        max-width: 400;
        margin-top: 0;
        max-height: 1500;
        font-family: 'Ceviche One';
}

.dragonTitle {
    font-size: 80px;
    color: #FFE433;
    text-shadow: 5px 5px red;
}
html, body {
    max-width: 100%;
    overflow-x: hidden;
}

@media (min-width: 767px) {
    html, body {
        max-width: 100%;
        overflow-x: hidden;
    }
    .dragonTitle {
        font-size: 100px;
        color: #FFE433;
        text-shadow: 5px 5px red;
    }
    .infoPanel {
        padding: 15px;
        padding-top: 45px;
        padding-bottom: 45px;
        background-color: antiquewhite;
        margin-left: 0px;
        margin-right: 0px;
        display: flex;
        color: #796059;
    }
    .infoPanel-text h1 {
        font-size: 50px;
        color: saddlebrown;
        margin-bottom: 30px;
        box-shadow: none;
        margin-left: 0px;
    }
    .infoPanel-text h3 {
        font-size: 40px;
        box-shadow: none;
        margin-left: 0px;
    }
    .infoPanel-img {
        height: 400px;
    }
    .header-text h3 {
        font-size: 40px;
        margin-top: 20px;
        margin-bottom: 20px;
        color: #FFE433;
        text-shadow: 2px 1px red;
    }
    .header-img {
        margin: 0px;
        width: 150px;
        height: 150px;
    }
    .header-text h1 {
        color: #6fd7f0;
        margin-left: 20vw;
    }
    .mintButton {
        flex-direction: "column";
        background-color: "#ff8932";
        padding: 24;
        padding-right: 10;
        padding-top: 0;
        border-style: "solid";
        border-color: "black";
        border-width: 10;
        border-radius: 10;
        font-size: 40;
        max-width: 400;
        margin-top: 0;
        max-height: 1500;
        font-family: 'Ceviche One';
}
.infoPanel-text h1 {
    color: saddlebrown;
    margin-bottom: 30px;
    box-shadow: none;
}
.infoPanel-text h3 {
    font-size: 40px;
    box-shadow: none;
}
}